<template>
    <div class="container mt-5">
      <h2 class="mb-4">회원가입(가게)</h2>
      <form @submit.prevent="submitForm">
        <div class="mb-3">
          <label for="name" class="form-label">이름</label>
          <input
            type="text"
            v-model="name"
            class="form-control"
            id="name"
            required
          />
        </div>
        <div class="mb-3">
          <label for="businessName" class="form-label">상호명</label>
          <input
            type="text"
            v-model="businessName"
            class="form-control"
            id="businessName"
            required
          />
        </div>
        <div class="mb-3">
          <label for="email" class="form-label">이메일(더치오더 아이디로 사용됩니다)</label>
          <input
            type="email"
            v-model="email"
            class="form-control"
            id="email"
            required
          />
        </div>
        <div class="mb-3">
          <label for="password" class="form-label">비밀번호</label>
          <input
            type="password"
            v-model="password"
            class="form-control"
            id="password"
            required
          />
        </div>
        <div class="mb-3">
          <label for="confirmPassword" class="form-label">비밀번호 확인</label>
          <input
            type="password"
            v-model="confirmPassword"
            class="form-control"
            id="confirmPassword"
            required
          />
        </div>
        <div class="mb-3">
        <label for="phoneNumber" class="form-label">연락처</label>
        <input
          type="tel"
          v-model="phoneNumber"
          @input="formatPhoneNumber"
          class="form-control"
          id="phoneNumber"
          pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
          required
        />
        <small class="text-muted">예시: 010-1234-5678</small>
      </div>
        <!-- 사업자용 첨부 파일 입력 -->
      <div class="mb-3">
        <label for="businessLicense" class="form-label">사업자 등록증 첨부</label>
        <input type="file" @change="handleFileUpload" class="form-control" id="businessLicense" accept=".pdf, .jpg, .png" required />
        <!-- accept 속성을 사용하여 허용할 파일 형식을 지정할 수 있습니다. -->
      </div>
      
      <button type="submit" class="btn btn-primary">가입하기</button>
    </form>
    </div>
  </template>
  
  <script>
  export default {
      name: "B_Join",
    data() {
      return {
        name: "",
        businessName: "",
        email: "",
        password: "",
        confirmPassword: "",
        phoneNumber: "",
      };
    },
    methods: {
      submitForm() {
        console.log(
          "가입 정보:",
          this.name,
          this.businessName,
          this.email,
          this.password,
          this.phoneNumber
        );
        
        // 여기에 실제 회원가입 로직을 추가하세요
      },
      formatPhoneNumber() {
      // 숫자만 남기고 "-"를 추가하여 형식에 맞게 변환
      let formattedNumber = this.phoneNumber.replace(/\D/g, "").replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3");
      // 형식에 맞게 변경된 연락처를 다시 모델에 할당
      this.phoneNumber = formattedNumber;
    },
      handleFileUpload(event) {
      this.businessLicenseFile = event.target.files[0];
      console.log("사업자 등록증 파일:", this.businessLicenseFile);
    },
    },
  };
  </script>
  
  <style scoped>
  .container {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* 최소 높이 설정 */
  }
  
  .mb-4 {
    text-align: center;
  }
  
  </style>